.container{
    width:100%;
    height:auto;
    background-color: white;
    border-bottom: 1px solid black;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.content{
    width:30%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    /* border: 1px solid black; */
    font-size: 18px;
    font-weight: 700;
}

.cartItembtn{
    width:30%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2px 8px;
    border-radius: 10px;
    /* border: 1px solid black; */
    margin-bottom: 10px;
   
}
.minusbtn{
    width: auto;
    height: auto;
    font-size: 24px;
    font-weight: 900;
    color:black;
    padding: 6px 20px;
    border-radius: 8px;
    border: 2px solid rgb(19, 19, 19);
    background-color: rgb(148, 243, 163);
}
.plusbtn{
     width: auto;
    height: auto;
    font-size: 24px;
    font-weight: 900;
    color:black;
    padding: 6px 20px;
    border-radius: 8px;
    border: 2px solid rgb(19, 19, 19);
    background-color: rgb(148, 243, 163);
   
}

h2{
    color:orange;
}


@media (max-width: 767px){
    
    
    .content{
        font-size: 18px;
        font-weight: 400;
    }
    
    .cartItembtn{
        padding: 4px 8px;
        border-radius: 6px;
        /* border: 1px solid black; */
        margin-bottom: 6px;
       
    }
    .minusbtn{
        font-size: 18px;
        font-weight: 500;
        color:black;
        padding: 5px 12px;
        border-radius: 5px;
    }
    .plusbtn{
        font-size: 18px;
        font-weight: 500;
        color:black;
        padding: 5px 12px;
        border-radius: 5px;
    }
} 