.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #8a2b06;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 100px;
    box-sizing: border-box;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
}
.mealsimage {
  width: 100%;
  height: 35rem;
  z-index: 0;
  overflow: hidden;
}

.mealsimage img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}

@media (max-width: 767px){
  .header{
    padding: 0 50px;
}

.header h1{
  font-size: 22px;
}



}